@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.7.1/font/bootstrap-icons.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root{
  --color-dark: hsl(0, 0%, 15%);
  --color-light: white;
}
@font-face {
  font-family: "sofia-pro";
  src: url("/cornelia-ecom/static/media/Sofia Pro Light.4a146d1d.otf") format("opentype");
  font-weight: 300;
}

@font-face {
  font-family: "sofia-pro-med";
  src: url("/cornelia-ecom/static/media/Sofia Pro Medium.5b20abb3.otf") format("opentype");
  font-weight: 500;
}

body,
html {
  padding: 0;
  margin: 0;
  height: 100%;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.navbar {
  background-color: white !important;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  position: sticky;
  position: -webkit-sticky;
  z-index: 100;
  top: 0;
  color: #2d2d2d !important;

}


.navbar-brand {
  font-size: 1.6rem;
  font-family: 'Montserrat', sans-serif;
  margin-right: 1rem;
}


.navbar-nav .nav-link {
  padding-right: 0.3rem;
  padding-left: 0.3rem;
  font-size: 0.7rem;
}

.right-nav .nav-link{
  font-size: 0.85rem;
}

.right-nav .bi-person{
  font-size: 1.15rem ;
}

.right-nav .bi-bag{
  font-size: 0.95rem ;
}




@media (max-width: 992px) {
  
  .right-nav {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    border-top: 1px solid hsla(0, 0%, 83%, 0.486);
    grid-gap: 0;
    gap: 0;
    text-align: center;
    background-color: white;
  }
  .right-nav .nav-link {
    padding: 1rem;
    border-right: 1px solid hsla(0, 0%, 83%, 0.486);
  }
}

.pushcanvas {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 200;
  transition: 0.5s;
  width: 0;
  overflow-x: hidden;
}

.pushcanvas-btn {
  position: fixed;
  background-color: var(--color-dark);
  top: 40%;
  outline: none;
  border: none;
  margin: 0;
  cursor: pointer;
  color: white;
  border-radius: 0 5px 5px 0;
  padding: 0.3rem 0.8rem;
  z-index: -1;
  transition: 0.5s;
  overflow: hidden;
}

.pushcanvas-btn i {
  font-size: 1.2rem;
}

.pushcanvas-closeicon {
  cursor: pointer;
  font-size: 0.75rem;
}

.pushcanvas-body {
  background-color: var(--color-dark);
  color: white;
  font-family: 'Alfa Slab One', cursive;
  font-weight: bold;
  font-style: italic;
}


.pushcanvas-body section {
  height: 300vh;
  
}

.pushcanvas.show {
  width: 400px;
}

.pushcanvas-content {
  transition: margin-left 0.5s;
}

.pushcanvas-content.show {
  margin-left: 400px;
}

.pushcanvas-backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: -1;
  background-color: #000;
  display: none;
  transition: opacity 0.15s linear;
  opacity: 0.2;
}

@media (max-width: 576px) {
  .pushcanvas-btn {
    padding: 0.2rem 0.6rem;
  }
  .pushcanvas-btn i {
    font-size: 0.75rem;
  }
  .pushcanvas.show {
    width: 70%;
  }

  .pushcanvas-header {
    padding: 0.3rem !important;
  }
}

@media (max-width: 992px) {
  .pushcanvas-content.show {
    margin-left: 0;
  }
}

.card {
  border: none;
  border-radius: 0;
  font-family: "sofia-pro";
  width: inherit;
  height: 45vh;
  background: transparent;
}

.card-header {
  position: absolute;
  border-radius: 0;
  border: none;
  background-color: transparent;
  top: 10px;
  left: -3px;
  right: 0;
  padding: 0;
}

.card .labels {
  font-size: 0.65rem;
  color: white;
  padding: 0;
  margin: 0;
}

.card-header .label {
  list-style-type: none;
  padding: 0;
  display: inline;
  padding: 1px 6.5px;
}

.card .card-footer {
  background: none;
  border: none;
  padding: 0;
}

.card .card-body {
  margin-bottom: 2px;
  flex-grow: 1;
  flex-shrink: 1;
  background-color: lightgrey;
}


.card .product-price {
  font-size: 1.05rem;
  font-family: "sofia-pro-med";
}

.card .secondary-price {
  font-size: 0.65rem;
  color: gray;
}

.card .bi {
  font-size: 1rem;
}

@media (max-width: 768px) {
  .card .card-name {
    display: none;
  }

  .card-footer {
    display: flex;
    align-items: center;
    grid-gap: 3px;
    gap: 3px;
  }
}

@media (max-width: 490px) {
  .d-none-xs{
    display: none;
  }
}

.carousel-item img {
  object-fit: cover;
  object-position: center;
}


main > .container .header{
  display: block;
} 

.carousel {
  background-color: lightgrey;
  height: 100vh;
  position: relative;
}

.carousel-content{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
  font-family: 'Alfa Slab One', cursive;
  padding: 3rem 5rem;
}

.categories{
  height: 100vh;
  background: rgb(36, 36, 36);
  max-height: auto;
  position: relative;
}

.categories > .row{
  width: 100%;
  height: auto;
  z-index: 10;
}

.category{
  height: 55vh;
  background-color: lightgrey;
  flex: none;
}


.categories > .background{
  color: white;
  position: absolute;
  font-family: 'Alfa Slab One', cursive;
  height: 100%;
  width: 100%;
  z-index: 4;
}



.products{
  font-family: 'Alfa Slab One', cursive;
  color: var(--color-dark);
  height: 100vh;
}


.products .title{
  font-weight: bold;
  font-style: italic;
  font-size: 1.75rem;
}

.products .subheader{
  font-family: 'Montserrat', sans-serif;
  font-size: 0.8rem;
}

.products > .row{
  height: auto;
}

.products > .row > .col{
  flex: 1 0 25%;
}


.banner{
  height: 100vh;
  background: gray;
}

.newsletter{
  background: black;
}

@media (max-width: 768px) {
  .carousel {
    height: 100vh;
    width: 100%;
  }

  .categories{
    height: auto;
  }

  .products{
    height: auto;
  }

  .products .labels{
    font-size: 0.5rem;
  }

  .products .product-price {
    font-size: 0.9rem;
  }
  
  .products .secondary-price {
    font-size: 0.55rem;
  }

  .products > .row > .col{
    flex: 1 0 100%;
  }
}



@media (min-width: 768px) {

  .category {
    flex: 1 0;
  }

  .products > .row > .col{
    flex: 1 0 40%;
  }
  
  .products .title{
    font-size: 2.3rem;
  }
}

@media (min-width: 991px) {

  .products > .row{
    overflow: hidden;
  }

  .products > .row > .col{
    flex: 1 0 20%;
  }
  
}

